import React, { useEffect, useRef, useState } from 'react'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { ref, set} from 'firebase/database'
import * as Yup from 'yup'
import db from '../firebase'
import {useFormik} from 'formik'
import '../sass/register.scss'
import logo from '../assets/logo.gif'

function Register({ listUser, handleRg, changeForm }) {

    const [valueSex,checkValueSex] = useState("1")
    //get ELement
    const inputUsername = useRef(null);
    const inputPassword = useRef(null);
    const inputNickname = useRef(null);
    const inputSex = useRef(null);

    //show/hide password
    const [eye, setEye] = useState('eye')
    const handleShowPassword = () => {
        if (eye === 'eye') {
            setEye('eye-slash')
            inputPassword.current.type = "text"
        }
        else {
            setEye('eye')
            inputPassword.current.type = "password"
        }
    }
    //formik react
    const formik = useFormik({
        initialValues: {
            userName: "",
            passWord: "",
            nickName: ""
        },
        validationSchema: Yup.object({
            userName: Yup.string()
                .required("Khng c b trng!")
                .min(4, "Phi c 4 k t tr ln")
                .max(16, "Phi b hn 16 k t")
                .matches(/^[a-z0-9_.]+$/, "Ti khon cha k t c bit!")
            ,
            passWord: Yup.string()
                .required("Khng c b trng!")
                .min(6, "Phi c 6 k t tr ln")
                .max(16, "Phi b hn 16 k t")
                .matches(/^[a-zA-Z0-9]+$/,
                    "Mt khu cha k t c bit!")
            ,
            nickName: Yup.string()
                .required("Khng c b trng!")
                .min(2, "Phi c 6 k t tr ln")
                .max(10, "Phi b hn 10 k t")
            ,
        }),
        onSubmit: (values, { resetForm }) => {
            resetForm()
            const checkRegister = listUser.filter(user => {
                return user.username == values.userName
            })
            if (values.userName.search('admin') != -1 || values.nickName.search('admin') != -1) {
                inputUsername.current.value=""
                inputNickname.current.value=""
                inputPassword.current.value = ""
                checkValueSex("1")
                toast.error("Khng s dng t kho 'admin'!!");
            }
            else {
                if (checkRegister.length < 1 || checkRegister == undefined) {
                    set(ref(db, `listAccount/${values.userName}`), {
                        username: values.userName,
                        password: values.passWord,
                        name: values.nickName,
                        sex:valueSex
                    })
                    inputUsername.current.value=""
                    inputNickname.current.value=""
                    inputPassword.current.value = ""
                    checkValueSex("1")
                    toast.success("ng k thnh cng!");
                }
                else {
                    inputUsername.current.value=""
                    inputNickname.current.value=""
                    inputPassword.current.value = ""
                    checkValueSex("1")
                    toast.error("ng k tht bi, ti khon  tn ti!!")
                }
            }
        
    },
})
return (
    <div className="register">
        <div className='register__container'>
            <ToastContainer
                position="top-left"
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                autoClose={3000}
            />
            <div className='register__quit'>
                <i className="fa-solid fa-xmark" onClick={handleRg}></i>
            </div>
            <div className='register__body'>
                <div className='register__header'>
                    <img src={logo} alt="logo"></img>
                    <span>Nice to meet you!</span>
                    <span>Sig up for a free account</span>
                </div>
                <div className='register__input'>
                    {/* <input placeholder='Username'></input>
                    <input placeholder='password'></input> */}
                    <form className='loginform'
                        onSubmit={formik.handleSubmit}>
                        <input 
                        type="text" 
                        placeholder='Username' 
                        name="userName" 
                        id="userName"
                        onChange={formik.handleChange}
                        ref={inputUsername}
                        autoComplete="off" 
                        ></input>
                        {
                        formik.errors.userName && (
                            <p className="error">{formik.errors.userName}</p>
                        )
                        }
                        <input 
                        type="password" 
                        placeholder='Password' 
                        name="passWord" 
                        id="passWord"
                        ref={inputPassword}
                        onChange={formik.handleChange}
                        >
                        </input>
                        {/* <i className="fa-solid fa-eye-slash"></i> */}
                            <i className={`fa-solid fa-${eye}`} onClick={handleShowPassword}></i>
                        {
                        formik.errors.passWord && (
                            <p className="error1">{formik.errors.passWord}</p>
                            )
                        }
                        <input 
                        type="text" 
                        placeholder='NickName' 
                        name="nickName" 
                        id="nickName"
                        ref={inputNickname}
                        onChange={formik.handleChange}
                        autoComplete="off" 
                        >
                        </input>
                        {
                        formik.errors.nickName && (
                            <p className="error2">{formik.errors.nickName}</p>
                        )
                        }
                        <div className='register__sex'>
                            <select ref={inputSex} value={valueSex} onChange={e=>checkValueSex(e.target.value)}>
                                <option value="1">Nam</option>
                                <option value="2">N</option>
                            </select>
                        </div>
                        <button type="submit">Sign up</button>
                    </form>
                    <span>Already have an account? <p onClick={changeForm}>Log In</p></span>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Register